.user-cabinet {
    &__wrap {
        padding: 20px 0 80px 0;

        @media (min-width: 992px) {
            padding-top: 35px;
            padding-bottom: 97px;
        }
    }

    &__link-back {
        display: flex;
        align-items: center;
        margin: 40px 0;
        width: 100%;
        height: 40px;

        @media (min-width: 992px) {
            padding-bottom: 3px;
        }

        //a {
        //    position: relative;
        //    font-family: "GothamPro-Bold", sans-serif;
        //    font-size: 15px;
        //    font-weight: 400;
        //    color: black;
        //    text-transform: uppercase;
        //    text-decoration: none !important;
        //
        //    &::after {
        //        content: '';
        //        display: block;
        //        position: absolute;
        //        bottom: -1px;
        //        left: 0;
        //        width: 100%;
        //        height: 1px;
        //        background-color: black;
        //        opacity: 1;
        //        transition: opacity .1s linear;
        //    }
        //
        //    @media (min-width: 1200px) {
        //       &:hover::after {
        //           opacity: 0;
        //       }
        //    }
        //}

        a {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-width: 395px;
            margin: 0 auto;
            font-family: 'GothamPro-Medium', sans-serif;
            height: 40px;
            font-size: 12px;
        }
    }

    &__user-data {
        position: relative;
        width: 100%;

        ul {
            display: block;
            margin: 0;
            padding: 11px 0 34px 0;

            @media (min-width: 1200px) {
                padding-left: 40px;
                padding-bottom: 14px;
            }
        }

        li {
            display: block;
            margin: 0 0 25px 0;
            padding: 0;
            font-family: 'GothamPro', sans-serif;
            font-weight: 400;
            font-size: 15px;
            color: black;

            &:last-child {
                margin-bottom: 0;
            }

            a {
                position: relative;
                color: inherit;
                text-decoration: none;

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                    background-color: black;
                    opacity: .7;
                    transition: opacity .1s linear;
                }

                @media (min-width: 1200px) {
                   &:hover {
                       &::after {
                           opacity: 0;
                       }
                   }
                }
            }

            &.username {
                font-family: 'GothamPro-Bold', sans-serif;
            }
        }

        .btn {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            padding: 0;
            width: 100%;
            height: 40px;
            font-family: 'GothamPro-Medium', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;

            @media (min-width: 768px) {
                width: auto;
                padding-left: 47.5px;
                padding-right: 47.5px;
            }

            @media (min-width: 1200px) {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    &__orders {
        width: 100%;
        height: auto;

        @media (min-width: 1200px) {
            margin-top: -20px;
        }
    }

    &__no-orders {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: auto;

        @media (min-width: 1200px) {
            margin-top: -20px;
        }

        h3 {
            display: flex;
            align-items: center;
            margin: 0 0 20px 0;
            padding: 0 10px 3px 10px;
            width: 100%;
            height: 40px;
            border: 1px solid rgba(0,0,0,.6);
            font-family: 'GothamPro', sans-serif;
            font-weight: 400;
            font-size: 15px;
            text-transform: uppercase;
            color: black;

            @media (min-width: 1200px) {
                display: none;
            }
        }

        p {
            display: block;
            margin: 0 0 -9px 0;
            padding: 0;
            font-family: 'GothamPro-Medium', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 120%;
            text-align: center;
            color: black;

            @media (min-width: 1200px) {
                font-size: 23px;
                line-height: 130%;
                margin-top: 53px;
                margin-bottom: 39px;
            }
        }
    }

    &__orders-table {
        width: 100%;
        border-spacing: 0;
        border-collapse: collapse;
        border: 1px solid rgba(0,0,0,.6);
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        &_headers {
            display: none;

            @media (min-width: 1200px) {
                display: table;
            }
        }

        tr {
            display: flex;
            flex-direction: column;
            
            @media (min-width: 1200px) {
                display: table-row;
                width: 100%;
            }
        }

        th, td {
            @media (min-width: 1200px) {
                &.order {
                    width: 13.10344827586207%; // width: 190px;
                }

                &.made {
                    width: 13.10344827586207%; // width: 190px;
                }

                &.status {
                    width: 8.96551724137931%; // width: 130px;
                }

                &.delivery {
                    width: auto; // width: 400px;
                }

                &.list {
                    width: 18.62068965517241%; // width: 270px;
                }

                &.info {
                    width: 18.62068965517241%; // width: 270px;
                }
            }
        }

        th {
            height: 40px;
            vertical-align: middle;
            padding: 0 0 0 10px;
            border-right: 1px solid rgba(0,0,0,.6);
            font-family: 'GothamPro', sans-serif;
            font-weight: 400;
            font-size: 11px;
            text-transform: uppercase;
            line-height: 1.2;

            &:last-child {
                border: none;
            }

            @media (max-width: 1999px) {
                width: 100%;
            }

            @media (min-width: 1511px) {
                font-size: 15px;
            }
        }

        td {
            display: flex;
            flex-direction: column;
            border: none;
            width: 100%;
            border-bottom: 1px solid rgba(0,0,0,.6);
            padding: 0 10px 15px;
            font-family: 'GothamPro', sans-serif;
            font-weight: 400;
            font-size: 15px;
            line-height: 21px;
            color: black;

            @media (min-width: 1200px) {
                display: table-cell;
                border-bottom: none;
                border-right: 1px solid rgba(0,0,0,.6);
                vertical-align: top;
                padding-top: 8px;
                padding-bottom: 16px;
            }

            &:last-child {
                border-bottom: none;
            }
        }

        strong {
            font-family: 'GothamPro-Bold', sans-serif;
        }

        ul {
            display: flex;
            flex-direction: column;
            margin: 0;
            padding: 28px 0 0 0;

            @media (min-width: 1200px) {
                padding: 26px 0 2px 0;
            }
        }

        li {
            margin-bottom: 4px;

            &:last-child {
                margin-bottom: 0;
            }

            input {
                margin: 0;
                padding: 0;
                background-color: transparent;
                border: none;
            }
        }

        a {
            color: inherit;
            position: relative;
            text-decoration: none;

            &::after {
                content: '';
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: rgba(0,0,0,.6);
                opacity: 1;
                transition: opacity .1s linear;
            }

            @media (min-width: 1200px) {
                &:hover {
                    &::after {
                        opacity: 0;
                    }
                }
            }
        }

        mark {
            display: flex;
            align-items: center;
            width: 100%;
            height: 40px;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            color: black;
            background-color: transparent;
            text-transform: uppercase;
            margin-bottom: 9px;
            padding-bottom: 5px;

            @media (min-width: 1200px) {
                display: none;
            }
        }

        button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-width: 360px;
            height: 30px;
            margin: 6px 0 -7px 0;
            padding: 0;
            background-color: white;
            border: 1px solid red;
            font-family: 'GothamPro-Medium', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            text-transform: uppercase;
            color: red;
            transition: all .1s linear;

            @media (min-width: 1200px) {
               &:hover {
                   background-color: red;
                   color: white;
               }
            }
        }

        p {
            @media (min-width: 1200px) {
               line-height: 1.2;
            }
        }
    }

    &__configurations {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        margin: 0;
        padding: 0;

        @media (min-width: 1200px) {
            margin-top: -20px;
        }

        li {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            background-color: white;
            border: 1px solid rgba(0,0,0,.6);
            margin: 0;
            padding: 19px 19px 20px 19px;

            @media (min-width: 768px) {
                flex-wrap: nowrap;
                column-gap: 20px;
                align-items: center;
            }

            @media (min-width: 1200px) {
                padding: 0;
                border: none;
                align-items: flex-start;
            }

            @media (min-width: 1511px) {
                column-gap: initial;
            }
        }

        button {
            display: block;
            position: relative;
            width: 48px;
            height: 48px;
            background-color: white;
            border: 1px solid rgba(0,0,0,.6);
            background-position: center !important;
            background-repeat: no-repeat !important;

            @media (min-width: 768px) {
                order: 2;
                flex: 1 0 auto;
            }

            @media (min-width: 1200px) {
                width: 40px;
                height: 40px;
            }

            @media (min-width: 1511px) {
                margin-left: 20px;
            }

            &.btn_text {
                @media (min-width: 1511px) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-image: none !important;
                    width: auto;
                    padding: 0 33px;

                    &:hover {
                        background: black;
                        color: white;
                    }
                }
            }

            &.btn_edit {
                background-image: url("data:image/svg+xml;base64,");

                @media (min-width: 1200px) {
                    background-image: url("data:image/svg+xml;base64,");
                }

                mark {
                    width: 153px;
                    height: 34px;
                    margin-left: 32px;

                    &::after {
                        margin-left: -32px;
                    }

                    @media (min-width: 768px) {
                        margin-left: initial;

                        &::after {
                            margin-left: initial;
                        }

                    }
                }
            }

            &.btn_cart {
                background-image: url("data:image/svg+xml;base64,");

                mark {
                    width: 115px;
                    height: 34px;
                }
            }

            &.btn_export {
                background-color: white;
                background-image: url("data:image/svg+xml;base64,");

                mark {
                    width: 94px;
                    height: 34px;
                }

                @media (min-width: 1200px) {
                    background-image: url("data:image/svg+xml;base64,");
                }

                @media (min-width: 1511px) {
                    &:hover {
                        background-color: black;
                        background-image: url("data:image/svg+xml;base64,");
                    }
                }
            }

            &.btn_close {
                background-color: white;
                background-image: url("data:image/svg+xml;base64,");

                mark {
                    width: 94px;
                    height: 34px;
                }

                @media (min-width: 1200px) {
                    background-image: url("data:image/svg+xml;base64,");
                }

                @media (min-width: 1511px) {
                    &:hover {
                        background-color: red;
                        background-image: url("data:image/svg+xml;base64,");
                    }
                }
            }

            span {
                display: none;
                font-family: 'GothamPro-Medium', sans-serif;
                font-weight: 400;
                font-size: 12px;
                text-transform: uppercase;
                color: inherit;

                @media (min-width: 1511px) {
                    display: block;
                }
            }

            mark {
                display: none;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: 0;
                left: 50%;
                background-color: black;
                transform: translate(-50%, -48px);
                font-family: 'GothamPro-Medium', sans-serif;
                font-weight: 400;
                font-size: 16px;
                color: white;
                text-transform: none;
                z-index: 1;

                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    z-index: 1;
                    border: 14px solid transparent;
                    border-top: 14px solid black;
                    bottom: -27px;
                    left: calc(50% - 14px);
                }
            }

            &.tooltip-visible {
                mark {
                    display: flex;
                }
            }
        }

        p   {
            display: block;
            margin: 20px 0 0 0;
            padding: 0;
            font-family: 'GothamPro-Bold', sans-serif;
            font-weight: 400;
            font-size: 15px;
            line-height: 104.5%;
            text-transform: uppercase;
            color: black;

            @media (min-width: 768px) {
                order: 1;
                margin-top: 0;
                line-height: 1.3;
                width: 100%;
            }

            @media (min-width: 1200px) {
                border: 1px solid rgba(0,0,0,.6);
                padding: 9.25px 20px;
            }

            @media (min-width: 1511px) {
                margin-right: 2px;
            }
        }
    }
}