.edit-user {
    &__wrap {
        display: flex;
        flex-direction: column;
        align-content: center;
        width: 100%;
        max-width: 692px;
        margin: 20px auto;

        @media (min-width: 992px) {
            margin-top: 40px;
            margin-bottom: 100px;
        }
    }

    &__container {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        &.collapsed {
            .edit-user__controller > div::after {
                background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/553/2bcfff979f.png");
            }

            .edit-user__content {
                height: 0;
            }

            .edit-user__form {
                opacity: 0;
                transform: translateY(-5px);
            }

            &:last-child {
                margin-bottom: 20px;
            }
        }
    }

    &__controller {
        & > div {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            width: 100%;
            height: 40px;
            border: 1px solid black;
            background-color: transparent;
            cursor: pointer;

            &::before, &::after {
                content: '';
                display: flex;
                align-items: center;
                justify-content: center;
                flex: 1 0 auto;
                position: relative;
                height: 100%;
                background-position: center;
                background-repeat: no-repeat;
            }

            &::before {
                width: 40px;
                background-color: black;
                transform: translateX(-1px);
            }

            &::after {
                width: 38px;
                border-left: 1px solid black;
                background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/552/3c38656ff4.png");
            }

            &.human::before {
                background-image: url("data:image/svg+xml;base64,");
            }

            &.key::before {
                background-image: url("data:image/svg+xml;base64,");
            }

            span {
                font-family: "GothamPro-bold", sans-serif;
                display: flex;
                align-items: center;
                width: 100%;
                height: 100%;
                padding: 0 0 2px 19px;
                font-weight: 400;
                font-size: 14px;
                color: black;
                text-transform: uppercase;

                @media (min-width: 992px) {
                    font-size: 15px;
                    padding-bottom: 3px;
                }
            }
        }
    }

    &__content {
        height: auto;
        overflow: hidden;
        transition: all .1s ease-out;
    }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        width: 100%;
        height: auto;
        padding-top: 10px;
        opacity: 1;
        transform: translateY(0);
        transition: all .3s ease-out;

        @media (min-width: 992px) {
            padding-top: 20px;
        }

        label {
            margin-top: 40px;
            margin-bottom: 30px !important;

            &::before {
                display: none !important;
            }

            input {
                padding-left: 19px;
            }

            mark {
                display: flex;
                align-items: center;
                position: absolute;
                top: -40px;
                left: 0;
                width: 100%;
                height: 40px;
                background-color: transparent;
                padding-bottom: 4px;
                transform: translateX(-1px);
                font-size: 15px;

                &.required {
                    &::before {
                        content: '';
                        color: red;
                        font-size: 20px;
                        margin-right: 3px;
                        transform: translateY(-5px);
                        margin-left: 2px;
                    }
                }
            }
        }
    }

    &__button {
        width: 100%;
        margin-bottom: 20px;

        @media (min-width: 992px) {
            width: 280px;
            margin-bottom: 29px;
        }
    }
}

.new_slider.new_slider_one-line {
    .new_slider_swiper .swiper-slide {
        width: 200px;

        @media (min-width: 768px) {
            width: 294px;
        }

        @media (min-width: 980px) {
            width: 33.3333333%;
        }

        @media (min-width: 1200px) {
            width: 25%;
        }

        @media (min-width: 1511px) {
            width: 294px;
        }
    }

    .new_slider_swiper .swiper-slide .unit {
        min-width: 100%;
        max-width: 100%;
    }
}