.change-pass {

    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        height: auto;
        max-width: 693px;
        padding-top: 18px;
        margin: 0 auto;

        @media (min-width: 1200px) {
            padding-top: 42px;
        }
    }

    &__caption {
        font-family: "GothamPro-Bold", sans-serif;
        display: flex;
        align-items: center;
        position: relative;
        width: 100%;
        height: 40px;
        margin: 0;
        padding: 0 0 0 60px;
        font-weight: 700;
        font-size: 14px;
        color: black;
        text-transform: uppercase;
        border: 1px solid black;

        @media (min-width: 1200px) {
            font-size: 15px;
        }

        &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 40px;
            height: 100%;
            background-color: black;
            background-repeat: no-repeat;
            background-position: center;
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__subtitle {
        display: block;
        margin: 27px 0 28px 0;
        padding: 0 0 0 0;
        font-weight: 700;
        font-size: 15px;
        line-height: 95%;
        color: black;
        letter-spacing: 0.1px;

        @media (min-width: 1200px) {
            font-size: 17px;
            line-height: 95%;
            margin: 32px 0 22px 1px;
        }
    }

    &__form {
        width: 100%;

        p {
            display: block;
            margin: 0;
            padding: 6px 0 0 0;
            font-weight: 400;
            font-size: 12px;
            line-height: 171%;
            color: black;

            @media (min-width: 1200px) {
                font-size: 15px;
                line-height: 105%;
                padding-top: 12px;
            }
        }

        mark {
            display: none;
            background-color: transparent;
            margin: 30px 0 0 0;
            padding: 0;
            font-weight: 400;
            font-size: 12px;
            line-height: 171%;
            color: #00960f;

            &.visible {
                display: block;
            }

            @media (min-width: 1200px) {
                font-size: 15px;
                line-height: 105%;
                margin-top: 54px;
            }
        }
    }

    &__button {
        margin-bottom: 70px;
        margin-top: 31px;

        @media (min-width: 1200px) {
            margin-top: 41px;
        }

        button {
            @media (min-width: 768px) {
                width: 280px !important;
            }
        }
    }

    &__message {
        display: none;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: auto;
        margin: 0;
        padding: 17px 30px 14px;
        font-family: 'GothamPro-Bold', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 121%;
        color: white;
        text-align: center;
        border-bottom-width: 4px;
        border-bottom-style: solid;

        @media (min-width: 768px) {
           font-size: 16px;
        }

        @media (min-width: 992px) {
            font-size: 18px;
            top: 50%;
            transform: translateY(-50%);
            padding: 14px 40px;
        }

        @media (min-width: 1200px) {
            font-size: 20px;
        }

        &.visible {
            display: flex;
        }

        &_default {
            background-color: #00960f;
            border-color: red;
        }

        &_bright {
            background-color: red;
            border-color: black;
        }

        &__dark {
            background-color: black;
            border-color: red;
        }
    }
}