.about-brand {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 65px;

    @media (min-width: 1200px) {
        margin-bottom: 120px;
    }

    &__banner {
        display: block;
        width: 100%;
        height: auto;
        margin: 0 0 38px 0;

        @media (min-width: 1200px) {
            margin-bottom: 66px;
        }
    }

    &__container {
        width: 100%;
        max-width: 1240px;
        margin: 0;
        padding: 0 20px;
    }

    &__title {
        font-family: 'GothamPro-Light', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 31px;
        line-height: 100%;
        text-align: center;
        color: #000000;
        margin: 0 0 22px 0;
        padding: 0;

        @media (min-width: 1200px) {
            font-size: 66px;
            margin-bottom: 103px;
        }
    }

    &__text {
        margin: 0;
        padding: 0;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.279;
        text-align: center;
        color: #000000;

        @media (min-width: 1200px) {
            font-size: 22px;
            line-height: 1.319;
        }
    }

    &__buttons {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 40px 0 0 0;
        padding: 0;

        @media (min-width: 1200px) {
            margin-top: 101px;
        }

        .btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 280px;
            height: 40px;
            border: 1px solid #000000;
            border-radius: 0;
            margin: 0 12px 16px;
            padding: 3px 12px 6px 12px;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 85.5%;

            @media (min-width: 1200px) {
                width: 465px;
                font-size: 18px;
                line-height: 20px;
                padding-top: 5px;
            }
        }
    }
    
    .d-none {
        display: none;
    }

    .d-xl-inline {
        @media (min-width: 1200px) {
            display: inline;
        }
    }

    .d-xl-none {
        @media (min-width: 1200px) {
            display: none;
        }
    }
}