.jcont {
  margin-left: auto;
  margin-right: auto;
  width: 1490px;
  padding-left: 20px;
  padding-right: 20px;
}

.cartcalc {
  position: relative;
}

.cartcalc_error {
  background: #ff0000;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #ffffff;
  font-size: 16px;
  width: 340px;
  top: 50px;
  padding: 15px 13px;
  font-size: 16px;
  line-height: 120%;
  left: 50%;
  z-index: 5;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}

.cartcalc_error .btn_close {
  width: 30px;
  height: 30px;
  border: none;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  background: url("../images/pop_close.svg") no-repeat center center;
  background-size: 20px auto;
  position: absolute;
  z-index: 4;
  right: 0px;
  top: 0px;
}

.cartcalc_error:before {
  position: absolute;
  top: -10px;
  width: 18px;
  height: 18px;
  background: #ff0000;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  content: "";
  left: 50%;
  margin-left: -7px;
}

.card_main_content_unit_bottom .cartcalc_error {
  top: initial;
  bottom: 60px;
  width: 230px;
}

