.page--account label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 40px;
  border: 1px solid black;
  margin-bottom: 30px;
}

.page--account label::before {
  content: '';
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 0 auto;
  position: relative;
  width: 40px;
  height: calc(100% + 2px);
  border: 1px solid black;
  background-position: center;
  background-repeat: no-repeat;
  transform: translateX(-1px);
}

.page--account label.name::before {
  background-image: url("data:image/svg+xml;base64,");
}

.page--account label.phone::before {
  background-image: url("data:image/svg+xml;base64,");
}

.page--account label.pass::before {
  background-image: url("data:image/svg+xml;base64,");
}

.page--account label.error {
  border-color: red;
}

.page--account label.error::before {
  border-right-color: red;
}

.page--account label.error span {
  display: block;
}

.page--account label.error em {
  color: red;
}

.page--account label span, .page--account label em {
  position: absolute;
  font-size: 13px;
  line-height: 95%;
  left: 0;
  top: 48px;
}

.page--account label span {
  display: none;
  color: red;
}

.page--account label em {
  display: block;
  color: black;
}

.page--account label.subscribe {
  display: inline-flex;
  justify-content: flex-start;
  width: auto;
  height: auto;
  border: none;
  font-weight: 700;
  font-size: 12px;
  line-height: 120%;
  color: black;
  margin: 12px 0 19px 0;
  padding: 0;
  cursor: pointer;
  letter-spacing: 0.2px;
}

@media (min-width: 992px) {
  .page--account label.subscribe {
    font-size: 14px;
  }
}

.page--account label.subscribe input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page--account label.subscribe input:checked + .checker {
  background-color: red;
}

.page--account label.subscribe input:checked + .checker::after {
  transform: scale(1);
}

.page--account label.subscribe .checker {
  position: relative;
  top: initial;
  left: initial;
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background: transparent;
  border: 1px solid red;
  transition: all .1s linear;
}

.page--account label.subscribe .checker::after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("data:image/svg+xml;base64,");
  transform: scale(0.7);
  transition: all .1s linear;
}

.page--account label.subscribe::before {
  display: none;
}

.page--account input {
