.log-out {
    &__text {
        position: relative;
        margin: 0;
        padding: 73px 20px 88px;
        font-family: 'GothamPro-Bold', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 121%;
        color: black;
        text-align: center;

        @media (min-width: 768px) {
            font-size: 20px;
        }

        @media (min-width: 992px) {
            font-size: 23px;
            line-height: 130%;
            padding-top: 85px;
            letter-spacing: -0.13px;
        }
    }
}