.footer-content {
    width: 100%;
    height: auto;
    margin: 30px 0 0 0;

    @media (min-width: 991px) {
        margin-top: 50px;
        margin-bottom: 50px;
    }

    h2 {
        margin: 20px 0;
        padding: 20px 0;
        font-family: "GothamPro-bold", sans-serif;
        font-weight: 700;
        font-size: 16px;
        color: #000;
        border-bottom: 1px solid #000;

        @media (min-width: 991px) {
            padding-top: 13px;
            padding-bottom: 15px;
            font-size: 26px;
        }
    }

    h3 {
        margin: 20px 0;
        padding: 20px 0;
        font-family: "GothamPro-bold", sans-serif;
        font-weight: 700;
        font-size: 16px;
        color: #000;
        border-bottom: 1px solid #000;

        @media (min-width: 991px) {
            display: inline-block;
            margin-top: 0;
            width: auto;
            min-width: 653px;
            max-width: 100%;
            padding-top: 13px;
            padding-bottom: 15px;
            font-size: 26px;
        }
    }

    p {
        display: block;
        margin: 20px 0;
        padding: 0;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #000;

        @media (min-width: 991px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    ul {
        margin: 20px 0;
        padding: 0 0 0 22px;
        list-style: none;

        @media (min-width: 991px) {
            padding-left: 25px;
        }

        li {
            display: block;
            position: relative;
            list-style: none;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 18px;
            color: #000;

            @media (min-width: 991px) {
                font-size: 16px;
                line-height: 20px;
            }

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 1px;
                left: -13px;
                color: #000;

                @media (min-width: 991px) {
                    left: -16px;
                }
            }
        }
    }

    ol {
        margin: 20px 0;
        padding: 0 0 0 2px;
        list-style-type: auto;
        list-style-position: inside;

        li {
            position: relative;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 18px;
            color: #000;

            @media (min-width: 991px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
    }

    b {
        font-family: "GothamPro-bold", sans-serif;
        font-weight: 700;

        i {
            font-family: "GothamPro-BoldItalic", sans-serif;
            font-weight: 700;
        }
    }

    i {
        font-family: "GothamPro-Italic", sans-serif;
        font-style: italic;

        b, strong {
            font-family: "GothamPro-BoldItalic", sans-serif;
            font-weight: 700;
        }
    }

    strong {
        color: #ff0000;
        font-family: "GothamPro-bold", sans-serif;
        font-weight: 700;

        i {
            font-family: "GothamPro-BoldItalic", sans-serif;
            font-weight: 700;
        }
    }

    hr {
        display: block;
        margin: 59px 0 20px;
        padding: 0;
        width: 100%;
        height: 14px;
        background: #ff0000;

        @media (min-width: 991px) {
            margin-top: 21px;
        }
    }

    a {
        color: #2399e2;
        text-decoration: underline;

        &:hover {
            color: #2399e2;
            text-decoration: none;
        }
    }
}