.promocode {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 100%;
    height: auto;
    background: #ffffff;
    border: 1px solid #000000;
    margin: 0 auto 20px;
    padding: 9px 20px 19px;

    * {
        box-sizing: border-box;
    }

    @media (min-width: 1200px) {
       flex-direction: row;
        align-items: flex-start;
        padding: 20px 10px;
    }

    @media (min-width: 1440px) {
        padding: 19px 20px;
    }

    &__title {
        display: flex;
        align-items: center;
        width: 100%;
        height: 40px;
        margin: 0 0 10px;
        padding: 0;
        font-family: "GothamPro-Medium";
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 19px;
        text-transform: uppercase;
        color: #000000;

        @media (min-width: 1200px) {
            width: 250px;
            margin: 0;
            font-size: 13px;
        }

        @media (min-width: 1440px) {
            width: 310px;
            font-size: 15px;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;

        @media (min-width: 1200px) {
            flex-direction: row;
            align-items: flex-start;
            width: 100%;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        margin: 0 0 15px;
        padding: 0;
        width: 100%;
        height: auto;

        @media (min-width: 1200px) {
            margin: 0;
        }
    }

    &__body {
        display: flex;
        flex-direction: column;

        @media (min-width: 1200px) {
           flex-direction: row;
        }
    }

    &__value {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        width: 100%;
        height: 40px;
        margin: 0;
        padding: 0 10px;
        border: 1px solid #000000;
        background-color: #ffffff;
        text-align: left;
        outline: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-family: "GothamPro-Bold";
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 38px;
        text-transform: uppercase;
        color: #000000;

        @media (min-width: 1200px) {
            font-size: 13px;
        }

        @media (min-width: 1440px) {
            font-size: 15px;
        }
    }

    &__result {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin: -1px 0 0 0;
        padding: 8px 10px 9px 11px;
        width: 100%;
        height: auto;
        background: #ffffff;
        border: 1px solid #000000;
        font-family: "GothamPro-Medium";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 172.5%;
        text-transform: uppercase;
        color: #000000;

        @media (min-width: 1200px) {
            align-items: center;
            margin: 0 0 0 -1px;
            padding: 0 10px;
            width: auto;
            height: 40px;
            flex: 1 0 auto;
        }

        @media (min-width: 1440px) {
            padding: 0 34px;
        }

        &:empty {
            display: none;
        }

        &_accent {
           color: #ff0000;
        }
    }

    &__message {
        display: block;
        position: relative;
        margin: 9px 0 6px 0;
        padding: 0;
        font-family: "GothamPro-Medium";
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 131%;
        text-transform: uppercase;
        color: #000000;

        &::before {
            content: '*';
            display: inline-block;
            position: relative;
            margin: 0;
            padding: 0;

            font-family: "GothamPro-Medium";
            font-style: normal;
            font-weight: 400;
            font-size: 10px;
            line-height: 131%;
            text-transform: uppercase;
            color: #ff0000;
        }

        @media (min-width: 1200px) {
            margin: 18px 0 -2px 0;
        }
    }

    &__controlleers {
        display: flex;
        flex-direction: row;
        align-items: center;
        position: relative;
        width: 100%;
        height: 40px;

        @media (min-width: 1200px) {
            width: auto;
            flex: 1 0 auto;
        }
    }

    &__benefit {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1 0 auto;
        margin: 0;
        padding: 0;
        width: 50px;
        height: 100%;
        background: #ff0000;
        border: 1px solid #ff0000;
        font-family: 'GothamPro-Medium';
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 85.5%;
        text-transform: uppercase;
        color: #ffffff;

        @media (min-width: 1200px) {
            margin-left: -1px;
            border-color: #000000;
        }
    }

    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        font-family: 'GothamPro-Medium';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 85.5%;

        @media (min-width: 1200px) {
            width: auto;
            flex: 1 0 auto;
            margin-left: -1px;
            padding-left: 20px;
            padding-right: 20px;

            &:hover {
                background-color: #ffffff;
            }
        }

        @media (min-width: 1440px) {
            padding-left: 33px;
            padding-right: 33px;
        }
    }
    
    .d-md-none {
        @media (min-width: 768px) {
            display: none;
        }
    }
}

.card_characters_list-promo {
    .card_characters_list_title .btn {
        background-color: #eeeeee !important;
        background-position: 0 !important;
        color: #000000 !important;
        border: none;
    }

    .card_characters_list_content {
        padding-top: 0 !important;
    }
}