.reviews {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: auto;
    padding-bottom: 30px;

    &__title {
        display: flex;
        flex-wrap: wrap;
        margin: -7px 0 0 0;
        padding: 0;
        width: 100%;
        max-width: 1081px;
        height: auto;
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 1.1;
        color: #000;

        @media (min-width: 768px) {
            align-items: baseline;
            font-size: 30px;
            margin-top: 12px;
            transform: translateX(1px);
        }

        &-code {
            display: block;
            margin-top: 10px;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: #000;

            @media (min-width: 768px) {
                display: inline-block;
                margin-top: 0;
                margin-left: 20px;
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        padding-top: 36px;
        width: 100%;
        max-width: 1081px;
        height: auto;

        @media (min-width: 768px) {
            padding-top: 56px;
        }
    }

    &__item {
        width: 100%;
        height: auto;
        padding: 13px 14px 15px 14px;
        background: #fff;
        border: 1px solid #000;
        margin-bottom: 31px;

        @media (min-width: 768px) {
            display: flex;
            position: relative;
            padding: 18px 14px 20px 20px;
            margin-bottom: 40px;
            border-color: #c4c4c4;
        }
    }

    &__user {
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: auto;
        padding-bottom: 20px;
        border-bottom: 1px solid #000;

        @media (min-width: 768px) {
            width: auto;
            border-bottom: none;
            padding-bottom: 0;
            padding-right: 20px;
            border-right: 1px solid #000;
        }
    }

    &__avatar {
        width: 172px;
        height: 172px;

        @media (min-width: 768px) {
            width: 220px;
            height: 220px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__rating {
        display: flex;
        flex-direction: column;
        margin-top: 5px;
        transform: translateX(4px);

        @media (min-width: 768px) {
            flex-direction: row;
            position: absolute;
            top: 18px;
            right: 5px;
            margin: 0;
            transform: none;
        }
    }

    &__likes,
    &__unlikes {
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        @media (min-width: 768px) {
            margin-bottom: 0;
            margin-left: 17px;
        }

        .pic {
            width: 30px;
            height: 30px;
            border: 1px solid #000;
            margin-right: 7px;
            background-position: center;
            background-repeat: no-repeat;
            cursor: pointer;
        }

        .val {
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            color: #000;
        }
    }

    &__likes {
        .pic {
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__unlikes {
        .pic {
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__content {
        margin-top: 19px;

        @media (min-width: 768px) {
            width: 100%;
            margin-top: 0;
            padding-left: 19px;
        }
    }

    &__name {
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        color: #000;
        margin-bottom: 16px;
        padding: 0 7px;

        @media (min-width: 768px) {
            padding: 7px 0 0 0;
        }
    }

    &__date {
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: #c4c4c4;
        margin-top: 16px;
        margin-bottom: 18px;
        padding: 0 7px;

        @media (min-width: 768px) {
            padding: 0;
            margin-bottom: 9px;
        }
    }

    &__mark {
        display: flex;
        align-items: center;
        margin-top: 19px;
        margin-bottom: 18px;
        padding-left: 7px;

        @media (min-width: 768px) {
            margin-top: 9px;
            margin-bottom: 29px;
            padding-left: 0;
        }

        span {
            display: block;
            width: 30px;
            height: 30px;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,");

            &.active {
                background-image: url("data:image/svg+xml;base64,");
            }
        }
    }

    &__message {
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #000;
        margin-top: 18px;
        padding: 0 5px 3px 5px;

        @media (min-width: 768px) {
            margin-top: 29px;
            padding: 0 0 5px 0;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 1081px;
        height: auto;
        margin: 0;
        padding: 18px 18px 20px 19px;
        border: 1px solid #000;

        @media (min-width: 768px) {
            padding: 19px 18px 39px 20px;
        }
    }

    &__form-title {
        display: flex;
        align-items: center;
        margin-bottom: 9px;

        .pic {
            display: block;
            width: 30px;
            height: 30px;
            margin-right: 10px;
            flex: 1 0 auto;
            background-color: #000;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 768px) {
                width: 40px;
                height: 40px;
                margin-right: 20px;
            }
        }

        .txt {
            display: block;
            width: 100%;
            height: auto;
            padding-right: 100px;
            font-family: "GothamPro-Medium", sans-serif;
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 110%;
            text-transform: uppercase;
            color: #000;
            margin-top: -2px;

            @media (min-width: 768px) {
                font-size: 15px;
            }
        }
    }

    &__form-user {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
        margin-top: 22px;
        margin-bottom: 0;
        padding-bottom: 6px;
        border-bottom: 1px solid #c4c4c4;

        @media (min-width: 768px) {
            margin-top: 11px;
            padding-bottom: 9px;
            margin-bottom: 8px;
        }

        label {
            display: flex;
            flex-direction: column;
            margin-bottom: 43px;

            @media (min-width: 768px) {
                flex-direction: row;
                flex-wrap: wrap;
                align-items: center;
                margin-bottom: 30px;
            }

            &.has-error {
                input {
                    border-color: #ff0000;
                }

                .error-msg {
                    display: block;
                }
            }
        }

        .error-msg {
            display: none;
            font-family: 'GothamPro', sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 13px;
            color: #ff0000;
            margin: 8px 0 -21px;

            @media (min-width: 768px) {
                width: 100%;
                padding-left: 118px;
            }
        }
    }

    &__form-label {
        display: block;
        position: relative;
        padding-left: 14px;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        color: #000;
        margin-bottom: 12px;

        @media (min-width: 768px) {
            display: flex;
            justify-content: flex-end;
            width: 118px;
            margin: 0;
            padding: 0 21px 0 2px;
        }

        &::before {
            content: '';
            display: block;
            position: absolute;
            top: -7px;
            left: 3px;
            color: red;
            font-size: 17px;

            @media (min-width: 768px) {
                position: relative;
                left: 0;
                margin-right: 2px;
            }
        }
    }

    &__form-input {
        display: flex;
        align-items: center;
        width: 100%;
        height: 40px;
        border: 1px solid #000;
        padding: 0 0 2px 19px;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        color: #000;

        @media (min-width: 768px) {
            width: calc(100% - 118px);
            padding-bottom: 4px;
        }
    }

    &__form-data {
        display: flex;
        flex-direction: column;
        margin-top: 16px;

        @media (min-width: 768px) {
            margin-top: 22px;
        }
    }

    &__form-caption {
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        color: #000;

        @media (min-width: 768px) {
            font-size: 17px;
        }
    }

    &__form-text {
        margin-top: 10px;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 120%;
        color: #000;

        @media (min-width: 768px) {
            font-size: 17px;
            margin-top: 22px;
        }
    }

    &__form-mark {
        display: flex;
        align-items: center;
        column-gap: 10px;
        margin-top: 30px;

        @media (min-width: 768px) {
            column-gap: 12.5px;
            margin-top: 20px;
        }

        span {
            display: block;
            width: 40px;
            height: 40px;
            background-color: #eee;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,");
            cursor: pointer;

            &.active {
                background-color: #000;
            }
        }
    }

    &__form-txt {
        width: 100%;
        height: 240px;
        border: 1px solid #000;
        padding: 10px 19px;
        resize: none;
        margin-top: 22px;
        font-family: 'GothamPro', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 120%;
        color: #000;

        @media (min-width: 768px) {
            height: 205px;
            padding-top: 9px;
        }
    }

    &__form-submit {
        margin-top: 20px;

        button {
            width: 145px;
            height: 40px;
            font-size: 12px;

            @media (min-width: 768px) {
                width: 141px;
                padding-bottom: 2px;
            }
        }
    }
}