.billboard-conf {
    position: relative;
    background-color: #000;
    height: auto;
    max-height: 100px;
    transition: all .3s cubic-bezier(0.33, 1, 0.68, 1);
    overflow: hidden;
    opacity: 1;

    &.collapsed {
        max-height: 0;
        opacity: 0;

        p {
            top: -5px;
        }
    }

    &__body {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }

    &__text {
        display: block;
        position: relative;
        margin: 0;
        //padding: 11px 31px 13px 0;
        padding: 11px 0 13px 0;
        font-family: 'GothamPro-Medium', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: white !important;
        text-align: center;
        text-transform: uppercase;
        top: 0;
        transition: all .2s linear;
        text-decoration: none !important;

        @media (min-width: 1200px) {
            font-size: 20px;
            //padding: 10px 42px 10px 1px;
            padding: 10px 0 10px 0;
        }
    }

    &__close-btn {
        display: block;
        position: absolute;
        top: 0;
        right: 10px;
        width: 40px;
        height: 40px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml;base64,");
        border: none;
        background-color: transparent;
        z-index: 1;

       @media (min-width: 1200px) {
           right: 20px;
       }
    }
}