@import "login-and-registration";
@import "change-password";
@import "log-out";
@import "logged";
@import "edit-user-data";
@import "user-cabinet";

.page--account {
    .breadcrumbs .jcont {
        padding: 0 10px;
    }

    label {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        width: 100%;
        height: 40px;
        border: 1px solid black;
        margin-bottom: 30px;

        &::before {
            content: '';
            display: flex;
            align-items: center;
            justify-content: center;
            flex: 1 0 auto;
            position: relative;
            width: 40px;
            height: calc(100% + 2px);
            border: 1px solid black;
            background-position: center;
            background-repeat: no-repeat;
            transform: translateX(-1px);
        }

        &.name::before {
            background-image: url("data:image/svg+xml;base64,");
        }
        &.phone::before {
            background-image: url("data:image/svg+xml;base64,");
        }
        &.pass::before {
            background-image: url("data:image/svg+xml;base64,");
        }
        &.error {
            border-color: red;

            &::before {
                border-right-color: red;
            }

            span {
                display: block;
            }

            em {
                color: red;
            }
        }

        span, em {
            position: absolute;
            font-size: 13px;
            line-height: 95%;
            left: 0;
            top: 48px;
        }

        span {
            display: none;
            color: red;
        }

        em {
            display: block;
            color: black;
        }

        &.subscribe {
            display: inline-flex;
            justify-content: flex-start;
            width: auto;
            height: auto;
            border: none;
            font-weight: 700;
            font-size: 12px;
            line-height: 120%;
            color: black;
            margin: 12px 0 19px 0;
            padding: 0;
            cursor: pointer;
            letter-spacing: 0.2px;

            @media (min-width: 992px) {
                font-size: 14px;
            }

            input {
                position: absolute;
                width: 1px;
                height: 1px;
                margin: -1px;
                border: 0;
                padding: 0;
                white-space: nowrap;
                clip-path: inset(100%);
                clip: rect(0 0 0 0);
                overflow: hidden;

                &:checked + .checker {
                    background-color: red;

                    &::after {
                        transform: scale(1);
                    }
                }
            }

            .checker {
                position: relative;
                top: initial;
                left: initial;
                display: inline-block;
                width: 30px;
                height: 30px;
                margin-right: 10px;
                background: transparent;
                border: 1px solid red;
                transition: all .1s linear;

                &::after {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 100%;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-image: url("data:image/svg+xml;base64,");
                    transform: scale(0.7);
                    transition: all .1s linear;
                }
            }

            &::before {
                display: none;
            }
        }
    }

    input {
        display: block;
        width: 100%;
        height: 100%;
        padding: 0 10px 2px;
        font-weight: 400;
        font-size: 15px;
        color: black;
        border: none;
        text-align: left;
        outline: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &::-webkit-input-placeholder {
            color: #999999;
            opacity: 1;
            transition: opacity 0.2s ease;
        }

        &::-moz-placeholder {
            color: #999999;
            opacity: 1;
            transition: opacity 0.2s ease;
        }

        &:-moz-placeholder {
            color: #999999;
            opacity: 1;
            transition: opacity 0.2s ease;
        }

        &:-ms-input-placeholder {
            color: #999999;
            opacity: 1;
            transition: opacity 0.2s ease;
        }

        &:focus::-webkit-input-placeholder {
            color: #999999;
            opacity: 0;
            transition: opacity 0.2s ease;
        }

        &:focus::-moz-placeholder {
            color: #999999;
            opacity: 0;
            transition: opacity 0.2s ease;
        }

        &:focus:-moz-placeholder {
            color: #999999;
            opacity: 0;
            transition: opacity 0.2s ease;
        }

        &:focus:-ms-input-placeholder {
            color: #999999;
            opacity: 0;
            transition: opacity 0.2s ease;
        }
    }

    form {
        button {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 15px 34px;
            width: 100%;
            height: 40px;
            background: black;
            border: 1px solid black;
            transition: all .1s linear;
            font-weight: 400;
            font-size: 12px;
            line-height: 85.5%;
            text-transform: uppercase;
            color: white;

            @media (min-width: 1200px) {
                &:hover {
                    background-color: white;
                    color: black;
                }
            }
        }
    }

    .title {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @media (min-width: 992px) {
            flex-direction: row;
            align-items: center;
        }

        a {
            font-family: "GothamPro-Light", sans-serif;
            font-weight: 400;
            display: inline-flex;
            align-items: center;
            position: relative;
            font-size: 16px;
            line-height: 18px;
            color: black;
            margin-top: 12px;
            text-decoration: none;

            @media (min-width: 992px) {
                margin: 1px 0 0 42px;
                font-size: 21px;
                line-height: 20px;
            }

            @media (min-width: 1200px) {
                &:hover {
                    color: red;

                    &::before {
                        background-image: url("data:image/svg+xml;base64,");
                    }

                    &::after {
                        background-color: red;
                    }
                }
            }

            &::before {
                content: '';
                display: block;
                position: relative;
                width: 16px;
                height: 16px;
                margin-right: 5px;
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                background-image: url("data:image/svg+xml;base64,");

                @media (min-width: 992px) {
                    width: 20px;
                    height: 20px;
                    margin-right: 6px;
                }
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                bottom: 1px;
                left: 1px;
                width: 100%;
                height: 1px;
                background-color: black;

                @media (min-width: 1200px) {
                    bottom: -1px;
                }
            }

            &.default {
                &::before {
                    display: none;
                }
            }
        }
    }

    .listing_title {
        position: relative;
    }

    .d-none {
        display: none !important;
    }

    .d-lg-none {
        @media (min-width: 1200px) {
            display: none !important;
        }
    }

    .card_characters_list.active {
        .card_characters_list_content {
            overflow: initial !important;
        }
    }

    .card_characters_list_title {
        span {
            font-size: 15px;
        }
    }

    .card_characters_list_content {
        display: block;
    }

    @media (min-width: 1200px) {
        .d-lg-block {
            display: block !important;
        }

        .d-lg-none {
            display: none !important;
        }
    }
}