.newspage {
  background: var(--color-accent-background);
}

.newspage.pt140 {
  padding-top: 140px;
}

.newspage_elements {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  width: auto;
  margin-left: -10px;
  margin-right: -10px;
}

.newspage_card_wrap {
  min-width: calc(33.33% - 20px);
  margin-left: 10px;
  margin-right: 10px;
  max-width: calc(33.33% - 20px);
  margin-bottom: 20px;
  background: #ffffff;
  position: relative;
  border: 1px solid #c4c4c4;
  display: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.newspage_card_wrap:hover {
  border: 1px solid #000000;
}

.newspage_card_wrap.open {
  display: block;
}

.newspage_card_img {
  border-bottom: 1px solid #c4c4c4;
  display: block;
  position: relative;

  &::before {
    display: block;
    content: '';
    padding-bottom: 100%;
  }
}

.newspage_card_img:hover img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.newspage_card_img img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  width: 100%;
  height: 100%;
  object-fit: contain;
  -webkit-filter: grayscale(10%);
  filter: grayscale(10%);
}

.newspage_card_content {
  padding: 20px 20px 60px;
}

.newspage_card_title {
  display: block;
  font-size: 20px;
  line-height: 23px;
  position: relative;
  color: #000000;
  margin-bottom: 18px;
  padding-bottom: 16px;
  font-family: "GothamPro-Medium";
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.newspage_card_title:before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 60px;
  height: 2px;
  background: red;
  border-radius: 2px;
  left: 0;
  bottom: 0;
}

.newspage_card_title:hover {
  color: red;
  text-decoration: none;
}

.newspage_card_text {
  font-size: 14px;
  line-height: 130%;
  font-family: "GothamPro-Medium";
  margin-bottom: 15px;
}

.newspage_card_text p {
  margin-bottom: 20px;
  font-family: "GothamPro";
  line-height: 1.3;
}

.newspage_card_text p b {
  font-family: "GothamPro-Medium";
}

.newspage_card_date {
  font-size: 16px;
  line-height: 15px;
  margin-bottom: 13px;
  color: #8d8d8d;
  margin-bottom: 15px;
}

.newspage_card_buttons {
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 2;
}

.newspage_card_buttons .btn {
  padding: 0 35px;
  height: 40px;
  font-size: 12px;
  line-height: 38px;
}

.newspage .more_units {
  padding-top: 50px;
  margin-top: -30px;
  padding-bottom: 50px;
}

.newspage .more_units span {
  display: none;
}

.newspage .more_units span.active {
  display: block;
}

.news_single {
  padding-top: 50px;
  padding-bottom: 20px;
}

.news_single .newspage_card_title {
  font-size: 30px;
  line-height: 31px;
  margin-bottom: 21px;
  padding-bottom: 28px;
}

.news_single .newspage_card_title:before {
  width: 120px;
  height: 4px;
}

.news_single .newspage_card_date {
  font-size: 22px;
  font-family: "GothamPro-Medium";
  margin-bottom: 24px;
}

.news_single .newspage_card_text {
  font-size: 18px;
  margin-bottom: 0;
}

.news_single_wr {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.news_single_content {
  min-width: 50%;
  max-width: 50%;
  padding-right: 25px;
  border-right: 1px solid #c4c4c4;
}

.news_single_img {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  padding-left: 40px;
  min-width: 50%;
  max-width: 50%;
}

.news_single_img img {
  width: 100%;
}

@media (max-width: 992px) {
  .newspage_card_wrap {
    min-width: calc(50% - 20px);
    max-width: calc(50% - 20px);
  }
  .news_single .newspage_card_title {
    font-size: 20px;
    line-height: 23px;
    padding-bottom: 15px;
    margin-bottom: 17px;
  }
  .news_single .newspage_card_title:before {
    width: 60px;
    height: 2px;
  }
  .newspage .newspage_card_text {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    //-webkit-line-clamp: 5;
  }
  .news_single .newspage_card_date {
    margin-bottom: 13px;
    font-size: 16px;
    line-height: 15px;
  }
  .news_single .newspage_card_text {
    font-size: 14px;
    line-height: 130%;
    font-family: "GothamPro-Medium";
  }
  .newspage.pt140 {
    padding-top: 75px;
  }
}

@media (max-width: 768px) {
  .news_single_content {
    border: none;
  }
  .news_single .newspage_card_title {
    font-size: 20px;
    line-height: 23px;
    padding-bottom: 15px;
    margin-bottom: 17px;
  }
  .news_single .newspage_card_title:before {
    width: 60px;
    height: 2px;
  }
  .newspage_card_text {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    //-webkit-line-clamp: 5;
  }
  .news_single .newspage_card_date {
    margin-bottom: 13px;
    font-size: 16px;
    line-height: 15px;
  }
  .news_single .newspage_card_text {
    font-size: 14px;
    line-height: 130%;
    font-family: "GothamPro-Medium";
  }
  .news_single {
    padding-bottom: 0;
  }
  .newspage.pt140 {
    padding-top: 40px;
  }
  .news_single_content,
  .news_single_img {
    min-width: 100%;
    max-width: 100%;
    padding-left: 0;
  }
  .news_single_img {
    -webkit-box-ordinal-group: 0;
    -ms-flex-order: -1;
    order: -1;
    margin-bottom: 20px;
  }
  .newspage_card_title {
    font-size: 20px;
    line-height: 23px;
    padding-bottom: 15px;
    margin-bottom: 17px;
  }
  .newspage_card_date {
    margin-bottom: 13px;
  }
  .designer {
    margin-bottom: 85px;
  }
  .news_single_content {
    padding: 0px 20px;
  }
}

@media (max-width: 650px) {
  .newspage_card_wrap {
    min-width: calc(100% - 15px);
    max-width: calc(100% - 15px);
  }
}

@media (max-width: 450px) {
  .newspage_card_buttons .btn {
    width: 100%;
  }
  .newspage_card_buttons {
    right: 20px;
  }
}

.news-list {
  padding: 50px 0 100px;

  &__show-more {
    display: flex;
    align-content: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: auto;
    margin-top: 25px;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #c4c4c4;
      z-index: 0;
    }

    .btn {
      position: relative;
      width: 200px;
      height: 40px;
      z-index: 1;
      box-shadow: 0 0 0 10px white;
      font-size: 12px;
    }
  }
}
