.log-reg {
    padding-top: 15px;

    @media (min-width: 992px) {
        padding-top: 20px;
    }

    &__controllers {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 20px;
        position: relative;
        margin-bottom: 25px;

        @media (min-width: 992px) {
            column-gap: 66px;
            margin-bottom: 0;
            padding-bottom: 34px;

            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 50%;
                width: 1px;
                height: 100%;
                background-color: #c4c4c4;
            }
        }

        button {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            width: 100%;
            height: 40px;
            border: 1px solid black;
            background-color: white;
            font-weight: 400;
            font-size: 12px;
            color: black;
            text-transform: uppercase;
            transition: all .1s linear;

            @media (min-width: 992px) {
                justify-content: flex-start;
                border-color: #c4c4c4;
                padding-left: 39px;
                font-weight: 700;
                font-size: 15px;
                color: black !important;

                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: 13px;
                    transform: translateY(-50%);
                    margin-top: 1px;
                    width: 14px;
                    height: 14px;
                    border: 1px solid black;
                    border-radius: 50%;
                    box-shadow: inset 0 0 0 2px transparent;
                    transition: all .1s linear;
                }
            }

            &.active {
                background-color: black;
                color: white;
                pointer-events: none;

                @media (min-width: 992px) {
                    background-color: #eee;
                    border-color: #eee;

                    &::before {
                        background-color: black;
                        box-shadow: inset 0 0 0 2px #eee;
                    }
                }
            }
        }
    }

    &__form-list {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
        margin-bottom: 40px;

        @media (min-width: 992px) {
            flex-direction: row;
            justify-content: space-between;
            align-items: flex-start;
            column-gap: 66px;
        }
    }

    &__form {
        display: none;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        width: 100%;
        height: auto;

        @media (min-width: 992px) {
            display: flex;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: -10px;
                left: 0;
                width: 100%;
                height: calc(100% + 20px);
                background-color: white;
                opacity: 0.9;
                z-index: 1;
            }
        }

        &.active {
            display: flex;

            @media (min-width: 992px) {
                &::before {
                    display: none;
                }
            }
        }

        &_reg {
            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: -33px;
                width: 1px;
                height: 100%;
                background-color: #c4c4c4;
            }
        }

        mark {
            display: block;
            position: relative;
            background-color: transparent;
            padding: 0;
            margin: 10px 0 0;
            font-weight: 400;
            font-size: 12px;
            line-height: 120%;
            color: black;

            @media (min-width: 992px) {
                font-size: 14px;
                margin-top: 17px;
                padding-right: 110px;
            }

            a {
                color: #2399e2;
            }
        }
    }
    
    &__list {
        display: none;
        
        @media (min-width: 992px) {
            display: flex;
            flex-direction: column;
            margin: -5px 0 1px 0;
            padding: 0;
        }

        li {
            display: flex;
            align-items: center;
            position: relative;
            font-weight: 400;
            font-size: 15px;
            color: black;
            padding-left: 31px;
            margin-bottom: 33px;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 6px;
                left: 18px;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #c4c4c4;
            }
        }
    }

    &__button {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-top: 20px;
        width: 100%;

        @media (min-width: 768px) {
            justify-content: flex-start;
        }

        button {
            @media (min-width: 768px) {
                width: 220px !important;
            }
        }

        a {
            display: inline-block;
            position: relative;
            width: 100%;
            margin-top: 32px;
            font-weight: 400;
            font-size: 15px;
            line-height: 95%;
            color: black;
            text-decoration: underline;

            @media (min-width: 1200px) {
                width: auto;
                line-height: initial;
                margin-top: 0;
                margin-left: 20px;

               &:hover {
                   text-decoration: none;
               }
            }
        }

        &_log {
            @media (min-width: 1200px) {
                margin-top: 30px;
            }
        }
    }

    &__form-footer {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        width: 100%;
        margin-top: 20px;

        @media (min-width: 1200px) {
            margin-top: 23px;
        }

        p {
            width: 100%;
            font-weight: 400;
            font-size: 15px;
            line-height: 95%;
            color: black;
            margin: 0 0 20px 0;
            padding: 0;

            @media (min-width: 1200px) {
                padding-right: 70px;
                line-height: 200%;
                margin: 0;
            }
        }

        a {
            display: block;
            width: 48px;
            height: 48px;
            margin-right: 20px;
            border: 1px solid black;
            background-color: black;
            background-repeat: no-repeat;
            background-position: center;
            transition: all .1s linear;

            @media (min-width: 1200px) {
                width: 40px;
                height: 40px;
                background-color: white;

                &:hover {
                    background-color: black;

                    &.telegram {
                        background-image: url("data:image/svg+xml;base64,");
                    }

                    &.vk {
                        background-image: url("data:image/svg+xml;base64,");
                    }
                }
            }

            &.telegram {
                background-image: url("data:image/svg+xml;base64,");

                @media (min-width: 1200px) {
                    background-image: url("data:image/svg+xml;base64,");
                }
            }

            &.vk {
                background-image: url("data:image/svg+xml;base64,");

                @media (min-width: 1200px) {
                    background-image: url("data:image/svg+xml;base64,");
                }
            }

            @media (min-width: 1200px) {
                margin-top: 31px;
            }
        }
    }
}