.logged {

    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 19px;
        padding-bottom: 80px;

        @media (min-width: 992px) {
            padding-top: 55px;
            padding-bottom: 137px;
        }
    }

    &__subtitle {
        display: block;
        margin: 0 0 20px 0;
        padding: 0;
        font-family: 'GothamPro-Bold', sans-serif;
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        text-align: center;
        color: black;

        @media (min-width: 992px) {
            font-size: 23px;
            line-height: 130%;
            letter-spacing: -0.1px;
            margin-bottom: 18px;
        }
    }

    &__text {
        display: block;
        margin: 0 0 18px 0;
        padding: 0;
        font-family: 'GothamPro-Light', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        text-align: center;
        color: black;

        @media (min-width: 992px) {
            font-size: 21px;
            line-height: 131.5%;
            margin-bottom: 29px;
        }

        &_italic {
            font-family: 'GothamPro-LightItalic', sans-serif;
            font-style: italic;
        }
        
        span {
            @media (min-width: 992px) {
                display: block;
            }
        }
    }

    &__link {
        position: relative;
        color: #2399e2;
        text-decoration: none !important;

        &::after {
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: #2399e2;
        }

        @media (min-width: 1200px) {
           &:hover {
               color: #2399e2;

               &::after {
                   display: none;
               }
           }
        }
    }

    &__button {
        display: flex;
        justify-content: center;
        margin-top: 19px;
        width: 100%;

        @media (min-width: 992px) {
            margin-top: 31px;
        }

        .btn {
            font-family: 'GothamPro-Medium', sans-serif;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 40px;
            font-size: 12px;

            @media (min-width: 768px) {
                width: auto;
            }

            @media (min-width: 992px) {
                width: 395px;
            }
        }
    }
}